  leftOffsetY: -2.5,         // Â∑¶„Çµ„Ç§„Éâ„Éê„É≥„Éâ„ÅÆYÊñπÂêë„Ç™„Éï„Çª„ÉÉ„ÉàÔºà„É°„Ç§„É≥„Éê„É≥„Éâ„ÅÆoffsetY„Åã„Çâ„ÅÆËøΩÂä†„Ç™„Éï„Çª„ÉÉ„ÉàÔºâ
  rightOffsetY: 5,        // Âè≥„Çµ„Ç§„Éâ„Éê„É≥„Éâ„ÅÆYÊñπÂêë„Ç™„Éï„Çª„ÉÉ„ÉàÔºà„É°„Ç§„É≥„Éê„É≥„Éâ„ÅÆoffsetY„Åã„Çâ„ÅÆËøΩÂä†„Ç™„Éï„Çª„ÉÉ„ÉàÔºâ
  
